.header {
  padding: 16px;
  background-color: #7cc4e6;
  border-bottom: 3px solid #6eb0ce;
  height: 65px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.h1 {
  color: #fff;
  text-align: center;
  text-shadow: 0 2px 1px rgba(0, 0, 0, 0.16);
  margin: 0;
  font-size: 24px;
  letter-spacing: 0.9px;
  font-weight: lighter;
}



