.input {
  width: 100%;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  height: 54px;
  border-bottom: 1px solid #e2e2e2;
}

.input:after {
  content: '';
  position: absolute;
  bottom: -1px;
  height: 1px;
  background-color: #7cc4e6;
  width: 0;
}

.inputActive {
  composes: input;
}

.inputActive:after {
  width: 100%;
  -webkit-transition: 0.5s all ease;
  -o-transition: 0.5s all ease;
  transition: 0.5s all ease;
}

.inputInValid {
  composes: input;
  border-bottom: 1px solid #f35454;
}

.inputField {
  width: 100%;
  border: none;
  background-color: transparent;
  font-size: 16px;
  outline: none;
  margin-top: 22px;
  margin-bottom: 5px;
}

.inputLabel {
  position: absolute;
  top: 18px;
  left: 0;
  width: 100%;
  font-size: 16px;
  line-height: 2.375;
  letter-spacing: 0.4px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  pointer-events: none;
  color: rgba(0, 0, 0, 0.54);
  -webkit-transform-origin: 0 50%;
      -ms-transform-origin: 0 50%;
          transform-origin: 0 50%;
  -webkit-transition: all ease-out 0.2s;
  -o-transition: all ease-out 0.2s;
  transition: all ease-out 0.2s;
}

.inputLabelActive {
  composes: inputLabel;
  -webkit-transform: translate3d(0, -45%, 0) scale(0.7);
          transform: translate3d(0, -45%, 0) scale(0.7);
}

.selectContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
}

.selectContainerTitle {
  font-size: 11px;
  letter-spacing: 0.4px;
  text-align: left;
  color: rgba(0, 0, 0, 0.54);
}

.plus {
  width: 24px;
  height: 24px;
  position: absolute;
  right: 7px;
  top: 70%;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  background-image: url('../assets/img/plus-btn.svg');
  background-repeat: no-repeat;
  background-position: 50%;
}

.plus:hover {
  background-image: url('../assets/img/plus-btn-active.svg');
}

.minus {
  composes: plus;
  background-image: url('../assets/img/minus-btn.svg');
}
.minus:hover {
  background-image: url('../assets/img/minus-btn-active.svg');
}

.select {
  width: 100%;
  padding: 8px 0;
  background-image: url(../assets/img/arrow-icon.svg);
  background-repeat: no-repeat;
  -webkit-appearance: button;
  background-position: 97% center;
  background-repeat: no-repeat;
  border: 0;
  border-bottom: 1px solid rgba(163, 163, 163, 0.32);
  background-color: #fafafa;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.87);
  letter-spacing: 0.6px;
  overflow: hidden;
  outline: 0;
  border-radius: 0;
}

.selectSmall {
  width: 160px;
  margin-right: 16px;
}

.phoneGroup {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.checkbox {
  display: none;
}

.checkboxLabel {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-family: Helvetica, sans-serif;
  font-size: 11px;
  letter-spacing: 0.4px;
  color: rgba(0, 0, 0, 0.54);
}

.checkboxLabel::before {
  content: '';
  display: inline-block;
  width: 16px;
  margin-right: 6px;
  height: 16px;
  border-radius: 4px;
  background-color: #dbdbdb;
}

.checkbox:checked + .checkboxLabel:before {
  content: '\2714';
  font-size: 11px;
  color: #fff;
  background-color: #6a9be4;
  text-align: center;
  line-height: 15px;
}

.validationMessage {
  position: absolute;
}



