.container {
  padding: 0 40px;
}

.cards {
  margin-top: 35px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  counter-reset: list;
}

.card {
  width: 112px;
  margin-right: 34px;
}

.cardItem {
  position: relative;
  width: 112px;
  height: 112px;
  border: 1px solid #7cc4e6;
  border-radius: 6px;
  background-color: #fff;
}

.cardItem::before {
  counter-increment: list;
  content: counter(list);
  position: absolute;
  top: -14px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  text-align: center;
  line-height: 28px;
  width: 28px;
  height: 28px;
  background-color: #7cc4e6;
  border-radius: 50%;
  font-size: 16px;
  color: #fff;
  font-weight: 700;
}

.cardImgFirst {
  width: 67px;
  height: 50px;
  margin: 31px 23px;
}

.cardImgSecond {
  width: 82px;
  height: 31px;
  margin: 48px 15px;
}

.cardImgThird {
  width: 80px;
  height: 61px;
  margin: 26px 16px;
}

.cardDescription {
  font-size: 10px;
  line-height: 1.2;
  letter-spacing: 0.4px;
  text-align: center;
  color: rgba(0, 0, 0, 0.54);
}

.videoTitle {
  margin: 20px 0;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.36);
  font-family: Avenir, sans-serif;
}

.width {
  width: 50%;
}

.video {
  width: 300px;
}

@media all and (max-width: 425px) {
  .cards {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }

  .card {
    margin: 0 0 35px 0;
  }

  .video {
    width: 100%;
  }
}



