.wrapper {
  margin: 1rem auto 1.5rem auto;
  max-width: 450px;
}

.title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin: 0 0 1rem 0;
  font-size: 12px;
  font-weight: 900;
  color: rgba(0, 0, 0, 0.32);
  text-align: left;
  text-transform: uppercase;
  letter-spacing: 0.4px;
}

.title:before {
  content: '';
  display: inline-block;
  height: 1rem;
  margin-right: 0.5rem;
  border-left: 4px solid rgba(0, 0, 0, 0.32);
}



