.container {
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.containerInner {
  position: relative;
  padding: 0 20px;
}

.containerInner span::-moz-selection,
.containerInner br::-moz-selection,
.containerInner img::-moz-selection {
  background-color: transparent;
}

.containerInner span::selection,
.containerInner br::selection,
.containerInner img::selection {
  background-color: transparent;
}

.h2 {
  color: #7cc4e6;
}

.templateWrapper {
  display: table;
}

.buttonWrapper {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 39px 0;
  padding-left: 48px;
  background-color: #fcfcfc;
}

.button {
  padding: 10px 24px;
  border-radius: 4px;
  border: 0;
  background-color: rgb(179, 186, 81);
  font-family: Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
}

.button:hover {
  background-color: rgb(187, 192, 120);
}

.popup {
  position: relative;
  display: inline-block;
  font-family: Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #b3ba51;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
  opacity: 0;
  margin-left: 16px;
  -webkit-transform: translateX(-48px);
      -ms-transform: translateX(-48px);
          transform: translateX(-48px);
  will-change: transform;
  z-index: -1;
}

.isSavedPopup {
  composes: popup;
  opacity: 1;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
  z-index: 1;
}

@media all and (max-width: 768px) {
  .container {
    width: 100%;
    padding: 20px;
  }

  .button {
    position: static;
    margin: 20px 0;
  }

  .buttonWrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 0;
  }
}



