.container {
  padding: 20px 40px;
}

@media all and (max-width: 768px) {
  .container {
    padding: 20px;
  }
}

.policy {
  margin-top: 22px;
}

.toCustomWrapper {
  width: 450px;
  margin: 0 auto;
  padding: 20px 0;
}

@media all and (max-width: 768px) {
  .toCustomWrapper {
    width: 320px;
  }
}

.toCustom {
  padding: 10px 24px;
  border-radius: 4px;
  border: 0;
  background-color: #c3c3c3;
  font-family: Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  cursor: pointer;
  margin-right: 16px;
}



