.wrapper {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100vh;
}

.homeLeft,
.homeRight {
  width: 50%;
}

.homeRight {
  background-color: #fff;
}

@media all and (max-width: 768px) {
  .homeLeft,
  .homeRight {
    width: 100%;
  }
}

.tabsWrapper {
  position: absolute;
  width: 100%;
  top: -3px;
  left: 0;
  min-height: unset !important;
}

.tabsContainer {
  -ms-flex-pack: justify !important;
      justify-content: space-between !important;
}

.tabIndicator {
  display: none;
}

.tab {
  background-color: #6eb0ce !important;
  -ms-flex-positive: 0 !important;
      flex-grow: 0 !important;
  min-width: 60px !important;
  min-height: 65px !important;
}

.tab[aria-selected='true'] {
  background-color: #7cc4e6 !important;
}

.tabLeft {
  border-top-right-radius: 8px !important;
  background-image: url(../../assets/img/form-icon.svg);
  background-size: 50%;
  background-position: center;
  background-repeat: no-repeat;
}

.tabRight {
  border-top-left-radius: 8px !important;
  background-image: url(../../assets/img/preview-icon.svg);
  background-size: 50%;
  background-position: center;
  background-repeat: no-repeat;
}

.tabLabelContainer {
  padding: 4px 16px !important;
}

.tabLabel {
  color: #fff;
  font-size: 11px !important;
  text-transform: capitalize;
}

.toCustomWrapper {
  padding: 0 20px;
}

.toCustom {
  color: #7cc4e6;
}



